*{
    padding: 0;
    margin: 0;
}

.title{
    padding: 60px;
    font-size: 40px;
    color: #fff;
}

.details{
    margin-top: 20px;
    color: black;
    margin-bottom: 20px;
}

.container{
    background-color: rgb(64,113,245);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form{
    background-color: #ffffff;
    width: 300px;
    height: 200px;
    border-radius: 15px;
    margin-top: 20px;
}

.input{
    /*width: 100%;*/
    margin-inline: 10px;
    padding-block: 5px;
    padding-inline-end: 60px;
    padding-inline-start: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    border:1px solid black;
}

.button{
    background-color: #4071F5;
    color: white;
    font-size: 20px;
    padding-inline: 12px;
    padding-block: 6px;
    border-radius: 3px;
    border: 1px solid #4071F5;
}