.container{
    background-color: rgb(64,113,245);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title{
    padding-top: 30px;
    font-size: 40px;
    color: #fff;
}

.button{
    /*background-color: #4071F5;*/
    /*color: white;*/
    background-color: white;
    color: #4071F5;
    font-size: 20px;
    padding-inline: 12px;
    padding-block: 6px;
    border-radius: 3px;
    border: 1px solid #4071F5;
}

.desc{
    color: white;
    font-size: 20px;
    margin-bottom: 20px;
}

.buttonDiv{
    display: flex;
    flex-direction: row;
    gap: 20px;
}

.streamDiv{
    display: flex;
    flex-direction: row;
    gap: 100px;
}

.streamTitle{
    color: white;
    margin-top: 10px;
}